<template>
  <v-app-bar dark style="background-color: #161b22">
    <div v-if="!isRedirect && user" class="d-flex justify-center align-center">
      <UserIcon size="30" class="align-content-lg-start" />
      <h5 style="margin-left: 10px">{{ this.user.login }}</h5>
    </div>
    <v-spacer />
    <v-toolbar-title class="flex" style="position: absolute; left: 50%; transform: translateX(-50%)">
      <div class="d-flex justify-center align-center">
        <img src="../assets/logo.svg" alt="tithonus" class="mr-3" />
        <h4>TITHONUS</h4>
      </div>
    </v-toolbar-title>
    <div>
      <v-menu v-model="menu" :close-on-content-click="false" :nudge-width="200" offset-x>
        <template v-slot:activator="{ on, attrs }">
          <v-btn v-bind="attrs" v-on="on"> {{ isRedirect ? "Custom GitHub Username" : "Change User" }} </v-btn>
        </template>

        <v-card>
          <v-form style="margin: 0 10px" ref="form" v-model="valid" lazy-validation>
            <v-text-field label="Username" v-model="name" :rules="rules" required></v-text-field>
          </v-form>
          <v-card-actions>
            <v-spacer></v-spacer>

            <v-btn text @click="reset"> Cancel </v-btn>
            <v-btn color="primary" text @click="validate"> Confirm </v-btn>
          </v-card-actions>
        </v-card>
      </v-menu>
      <v-tooltip bottom>
        <template v-slot:activator="{ on }">
          <v-btn v-on="on" @click="logoutUsingStore" style="margin-left: 5px" v-if="!isRedirect && user">
            <v-icon color="white">mdi-exit-to-app</v-icon>
          </v-btn>
        </template>
        <span>Logout</span>
      </v-tooltip>
    </div>
  </v-app-bar>
</template>

<script>
import Vue from "vue";
import { mapActions, mapGetters, mapMutations } from "vuex";
import UserIcon from "./UserIcon";
const { VUE_APP_CLIENT_ID } = process.env;
export default Vue.extend({
  name: "Appbar",
  components: { UserIcon },
  data: () => ({
    menu: false,
    valid: true,
    name: "",
    rules: [(value) => !!value || "Required."],
  }),
  computed: {
    ...mapGetters(["user"]),
    isRedirect() {
      return this.$route.name === "Redirect";
    },
  },
  methods: {
    ...mapMutations(["SET_DEFAULT_STATE"]),
    ...mapActions(["logout"]),
    async validate() {
      if (this.$refs.form.validate()) {
        await this.SET_DEFAULT_STATE();
        window.location.replace(
          `https://github.com/login/oauth/authorize?client_id=${VUE_APP_CLIENT_ID}&redirect_uri=${
            window.location.origin + (process.env.NODE_ENV === "production" ? process.env.VUE_APP_PROD_BASE_URL : "/")
          }&login=${this.name}`
        );
      }
    },
    logoutUsingStore() {
      this.logout();
    },
    reset() {
      this.menu = false;
      this.$refs.form.reset();
    },
  },
});
</script>

<style></style>
